<template>
  <div class="hello">
    <h1>Hello 列表</h1>
    <ul>
    	<li v-for="item in blogList">
        <router-link :to="'/blog/'+item.id">{{item.title}}</router-link>
      </li>
    </ul>
 </div>
</template>
<script type="text/javascript">
  export default {
    data(){
      return {
        blogList:[
          {id:1,title:'Html与Css基础'},
          {id:2,title:'Java从入门到放弃'},
          {id:3,title:'Vue-Cli全家桶'},
          {id:4,title:'Uiew_ui框架'},
          {id:5,title:'从零开始学Javascript语言'}

        ]
      }
    }
  }
</script>
<style>
  ul>li{
   list-style: none;
  }
</style>



