<template>
  <div>
  <h1>博客详情:{{ $route.params.id }}</h1>
  <p>
    {{blogContent}}
  </p>
  <!-- <h1>{{ blogContent }}</h1> -->
  </div>
</template>

<script>
  export default {
    data(){
      return {
        blogContent:""
      }
    },

    created(){
      let id = this.$route.params.id;
      this.getBlogDataById(id);
    },

    methods: {
      getBlogDataById(id){
        switch(id){
          case "1":this.blogContent = "Web前端进阶课程";break;
          case "2":this.blogContent = "Javasript权威指南";break;
          case "3":this.blogContent = "精通Node.js";break;
          case "4":this.blogContent = "Vue";break;
          case "5":this.blogContent = "微信小程序";break;
        }
      }
    }
  }
</script>

<style>
</style>



