<template>
	<div>
		<form @submit.prevent="login">
			<div>
        <span>
            <label>用户名</label>
        </span>
				<input type="text" v-model="username" placeholder="admin">
			</div>
      <div>
        <span id="">
            <label>密码</label>
        </span>
        <input type="text" v-model="password" placeholder="123456">
        <div>
            <button type="submit">确定</button>
        </div>
      </div>
		</form>
	</div>
</template>
<script type="text/javascript">
export default{
  data(){
    return{
        username:'',
        password:''
    }
  },
  methods:{
    login(){
        if(this.username=='admin' && this.password == "123456")
        {
          this.$router.push("/hello");
        }else{ alert("error");}

    }

  }
}
</script>
<style>
  div{
    text-align: left;
  }
  span{
    display: inline-block;
    width: 100px;
  }
</style>



